import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import IconButton from '@mui/material/IconButton';
import useMediaQuery from '@mui/material/useMediaQuery';
import HomeIcon from '@mui/icons-material/Home';

import TopBar from '../components/topbar';
import Footer from '../components/footer';
import BurgerMenu from '../components/topbar/BurgerMenu';
import { MiscLinks } from '../components/topbar/misclinks';

function DefaultBackground({ children }) {
  return (
    <Box
      sx={{
        backgroundColor: 'basepage.background',
        overflow: 'auto',
        flex: 1,
        width: 1,
      }}
    >
      {children}
    </Box>
  );
}

function HomeButton() {
  return (
    <Link style={{ textDecoration: 'none' }} to="/memories">
      <IconButton sx={{ paddingTop: '0.3rem' }}>
        <HomeIcon fontSize="medium" sx={{ width: 45, height: 45, color: 'button.main' }} />
      </IconButton>
    </Link>
  );
}

function ProfileBar() {
  return (
    <Stack
      direction="row"
      justifyContent="space-between"
      alignItems="center"
      sx={{
        borderTop: 4,
        borderColor: 'secondary.main',
        backgroundColor: 'primary.main',
      }}
    >
      <BurgerMenu />
      <HomeButton />
      <MiscLinks />
    </Stack>
  );
}

export default function BasePage({ children }) {
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        flexWrap: 'no-wrap',
        height: '100vh',
        width: '100vw',
      }}
    >
      <TopBar />
      <DefaultBackground>{children}</DefaultBackground>
      {belowsm ? <ProfileBar /> : null}
      <Footer />
    </Box>
  );
}

DefaultBackground.propTypes = {
  children: PropTypes.node.isRequired,
};

BasePage.propTypes = {
  children: PropTypes.node.isRequired,
};
