import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';

import ImageSelector from './ImageSelector';
import ImagePreviewer from './ImagePreviewer';
import FormStepperNavigation from './FormStepperNavigation';
import BoxForm from './BoxForm';
import ErrorText from './ErrorText';

export default function ImageForm({
  imageDetails,
  thumbnail,
  handleSelectedImageChange,
  handleThumbnailChange,
  removeSelectedImage,
  handleNext,
  handlePrevious,
  isLast,
  isFirst,
  setDimensions,
  resizeHeights,
}) {
  const maxAllowedImages = 15;
  const minAllowedImages = 1;
  const [imageError, setImageError] = useState('');
  const [touched, setTouched] = useState(false);

  function isValid() {
    let valid = true;
    setImageError('');
    if (imageDetails.length < minAllowedImages) {
      setImageError('Need atleast one image to create a memory');
      valid = false;
    }

    if (imageDetails.length > maxAllowedImages) {
      setImageError(`Maximum Allowed Images per Memory: ${maxAllowedImages}`);
      valid = false;
    }

    return valid;
  }

  useEffect(() => {
    if (touched) isValid();
  }, [imageDetails, touched]);

  const handleSubmit = (handleCallback) => {
    if (isValid()) {
      handleCallback();
    }
  };

  const handleImageChange = (event) => {
    setTouched(true);
    handleSelectedImageChange(event, isValid);
  };

  return (
    <BoxForm>
      <Grid item>
        <ImageSelector
          handleChange={handleImageChange}
        />
      </Grid>
      <Grid item container direction="column" alignItems="center">
        <Typography>
          Total Images Selected:
          {imageDetails.length}
          /
          {maxAllowedImages}
        </Typography>
        <ErrorText text={imageError} />
      </Grid>
      <FormStepperNavigation
        handleNext={() => handleSubmit(handleNext)}
        handlePrevious={handlePrevious}
        isLast={isLast}
        isFirst={isFirst}
      />
      <Grid item>
        <ImagePreviewer
          imageDetails={imageDetails}
          handleRemoveImage={removeSelectedImage}
          thumbnail={thumbnail}
          handleThumbnailChange={handleThumbnailChange}
          setDimensions={setDimensions}
          resizeHeights={resizeHeights}
        />
      </Grid>
    </BoxForm>
  );
}

ImageForm.propTypes = {
  imageDetails: PropTypes.arrayOf(PropTypes.shape({
    url: PropTypes.string,
    name: PropTypes.string,
    size: PropTypes.number,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
  handleSelectedImageChange: PropTypes.func.isRequired,
  removeSelectedImage: PropTypes.func.isRequired,
  thumbnail: PropTypes.number.isRequired,
  handleThumbnailChange: PropTypes.func.isRequired,
  handleNext: PropTypes.func.isRequired,
  handlePrevious: PropTypes.func.isRequired,
  isLast: PropTypes.bool.isRequired,
  isFirst: PropTypes.bool.isRequired,
  setDimensions: PropTypes.func.isRequired,
  resizeHeights: PropTypes.arrayOf(PropTypes.number).isRequired,
};
