export default function TopBar() {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));

  if (belowsm) {
    return (
      <TopBarGridContainer>
        <BrandLink />
      </TopBarGridContainer>
    );
  }

  if (belowmd) {
    return (
      <TopBarGridContainer>
        <BurgerMenu />
        <BrandLink />
        <MiscLinks />
      </TopBarGridContainer>
    );
  }

  return (
    <TopBarGridContainer>
      <BrandLink />
      {/* <MyMemoriesButton /> */}
      <MiscLinks />
    </TopBarGridContainer>
  );
}
