const get = async (url, path, params = {}) => {
  let newUrl = `https://${url}${path}`;
  const options = {
    method: 'GET',
    credentials: 'include',
    headers: new Headers({ 'content-type': 'application/json' }),
  };
  newUrl += `?${new URLSearchParams(params).toString()}`;
  const resp = await fetch(newUrl, options);
  if (!resp.ok) {
    if (resp.status === 401) {
      document.cookie = 'userInfo=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;';
      return { status: 'unauthenticated' };
    }
    if (resp.status === 403) {
      return { status: 'forbidden' };
    }
  }

  return resp.json();
};

function getObjectUrl(bucket, authURL, key, method) {
  try {
    const path = '/api/presigned-url';
    const params = { key, bucket, method };
    return get(authURL, path, params).then((response) => {
      if (response.Error) {
        /* eslint-disable no-console */
        console.log(response.Error);
        return '';
      }
      return response.URL;
    });
  } catch (error) {
    return error;
  }
}

async function validateDetails(bucket, authURL, details) {
  try {
    const path = '/api/details/validate';
    const urlParams = { bucket };
    let apiUrl = `https://${authURL}${path}`;
    apiUrl += `?${new URLSearchParams(urlParams).toString()}`;
    const options = {
      method: 'POST',
      credentials: 'include',
      headers: new Headers({ 'content-type': 'application/json' }),
      body: details,
    };
    return await fetch(apiUrl, options).then((response) => {
      if (!response.ok) {
        return { status: 'error' };
      }
      return response.json();
    });
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

async function upload(bucket, authURL, key, body) {
  try {
    const putURL = await getObjectUrl(bucket, authURL, key, 'PUT');
    const params = { body, method: 'PUT' };
    const resp = await fetch(putURL, params).then((response) => response);
    return resp;
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

async function deleteMemory(bucket, authURL, memoryID) {
  try {
    const path = '/api/memory';
    const params = { bucket, memory_id: memoryID };
    let apiUrl = `https://${authURL}${path}`;
    apiUrl += `?${new URLSearchParams(params).toString()}`;
    const options = {
      method: 'DELETE',
      credentials: 'include',
      headers: new Headers({ 'content-type': 'application/json' }),
    };
    return await fetch(apiUrl, options).then((response) => {
      if (!response.ok) {
        return { status: 'error' };
      }
      return { status: 'ok', body: response.json() };
    });
  } catch (error) {
    return error;
  }
}

function getThumbnails(bucket, authURL, memories) {
  try {
    const path = '/api/memory/thumbnail';
    return memories.map(async (memoryID) => {
      const params = { bucket, memory_id: memoryID };
      return get(authURL, path, params);
    });
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

function uploadMultipleImages(bucket, authURL, paramList, incrementImageCount) {
  try {
    return paramList.map(async (params) => {
      const resp = await upload(bucket, authURL, params.Key, params.Body);
      incrementImageCount();
      return resp;
    });
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

function getMemories(bucket, authURL, nextToken) {
  try {
    const path = '/api/memories';
    const params = { bucket };

    if (nextToken) {
      params.next_token = nextToken;
    }

    return get(authURL, path, params);
  } catch (error) {
    return error;
  }
}

function viewMemory(bucket, authURL, memoryKey) {
  try {
    const path = '/api/memory';
    const params = { bucket, memory_id: memoryKey };

    return get(authURL, path, params);
  } catch (error) {
    return error;
  }
}

function finalizeMemory(bucket, authURL, memoryKey) {
  try {
    const path = '/api/memory/finalize';
    const params = { bucket, memory_id: memoryKey };
    return get(authURL, path, params);
  } catch (error) {
    return error;
  }
}

async function updateMemoryDetails(bucket, authURL, memoryID, item, value) {
  try {
    const path = '/api/memory';
    const urlParams = { bucket, memory_id: memoryID };
    let apiUrl = `https://${authURL}${path}`;
    apiUrl += `?${new URLSearchParams(urlParams).toString()}`;
    const options = {
      method: 'PATCH',
      credentials: 'include',
      headers: new Headers({ 'content-type': 'application/json' }),
      body: JSON.stringify({ item, value }),
    };
    return await fetch(apiUrl, options).then((response) => response);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return false;
  }
}

async function updateMemoryImage(bucket, authURL, memoryID, action, image) {
  try {
    const path = '/api/memory';
    const urlParams = { bucket, memory_id: memoryID };
    let apiUrl = `https://${authURL}${path}`;
    apiUrl += `?${new URLSearchParams(urlParams).toString()}`;
    const options = {
      method: 'PATCH',
      credentials: 'include',
      headers: new Headers({ 'content-type': 'application/json' }),
      body: JSON.stringify({ item: 'image', action, image }),
    };
    return await fetch(apiUrl, options).then((response) => response);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return false;
  }
}
export default {
  getObjectUrl,
  validateDetails,
  upload,
  uploadMultipleImages,
  getMemories,
  viewMemory,
  getThumbnails,
  finalizeMemory,
  deleteMemory,
  updateMemoryDetails,
  updateMemoryImage,
};
