import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import { ReactComponent as Logo } from '../static/images/icons/logo.svg';

export function BrandLogo({ width, height }) {
  return <Logo width={width} height={height} />;
}

export function BrandText({ fontSize }) {
  return (
    <Typography
      color="secondary"
      variant="badscript"
      sx={{ paddingX: 1, fontSize, display: 'inline' }}
    >
      myMemoryStore
    </Typography>
  );
}

export function Branding({
  fontSize, width, height,
}) {
  return (
    <Grid item>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <BrandLogo width={width} height={height} />
        <BrandText fontSize={fontSize} />
      </Box>
    </Grid>
  );
}

Branding.defaultProps = {
  width: '64px',
  height: '64px',
  fontSize: '2em',
};

BrandLogo.propTypes = {
  width: PropTypes.string.isRequired,
  height: PropTypes.string.isRequired,
};

BrandText.propTypes = {
  fontSize: PropTypes.string.isRequired,
};

Branding.propTypes = {
  width: PropTypes.string,
  height: PropTypes.string,
  fontSize: PropTypes.string,
};
