import PropTypes from 'prop-types';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faYoutube,
  faFacebook,
  faTwitter,
  faInstagramSquare,
  faReact,
} from '@fortawesome/free-brands-svg-icons';

import { faHeart } from '@fortawesome/free-solid-svg-icons';

import { MutedText } from './common';

function YoutubeIcon({ size }) {
  return <FontAwesomeIcon icon={faYoutube} size={size} />;
}

function FacebookIcon({ size }) {
  return <FontAwesomeIcon icon={faFacebook} size={size} />;
}

function TwitterIcon({ size }) {
  return <FontAwesomeIcon icon={faTwitter} size={size} />;
}

function InstagramIcon({ size }) {
  return <FontAwesomeIcon icon={faInstagramSquare} size={size} />;
}

function ReactIcon() {
  return <FontAwesomeIcon icon={faReact} />;
}
function HeartIcon() {
  return <FontAwesomeIcon icon={faHeart} />;
}

export function HeartLink({ size, color }) {
  return (
    <SocialLink to="https://en.wikipedia.org/wiki/Love" color={color}>
      <HeartIcon size={size} />
    </SocialLink>
  );
}

export function ReactLink({ size, color }) {
  return (
    <SocialLink to="https://reactjs.org/" color={color}>
      <ReactIcon size={size} />
    </SocialLink>
  );
}

export function YoutubeLink({ size, color }) {
  return (
    <SocialLink
      to="https://www.youtube.com/channel/UCabGr_w5PLFIg1rcmKPbeVg"
      color={color}
    >
      <YoutubeIcon size={size} />
    </SocialLink>
  );
}

export function FacebookLink({ size, color }) {
  return (
    <SocialLink to="https://www.facebook.com/mymemorystore.in" color={color}>
      <FacebookIcon size={size} />
    </SocialLink>
  );
}

export function TwitterLink({ size, color }) {
  return (
    <SocialLink to="https://twitter.com/mymymemorystore/" color={color}>
      <TwitterIcon size={size} />
    </SocialLink>
  );
}

export function InstagramLink({ size, color }) {
  return (
    <SocialLink to="https://www.instagram.com/mymemorystore.in/" color={color}>
      <InstagramIcon size={size} />
    </SocialLink>
  );
}

export function SocialLink({ to, color, children }) {
  return (
    <Link
      underline="none"
      href={to}
      rel="noreferrer noopener"
      target="_blank"
      sx={{ px: 1, color: `${color}` }}
    >
      {children}
    </Link>
  );
}

export function SocialLinks({ size }) {
  return (
    <Grid item>
      <MutedText>Follow Us</MutedText>
      <YoutubeLink color="social.youtube" size={size} />
      <FacebookLink color="social.facebook" size={size} />
      <TwitterLink color="social.twitter" size={size} />
      <InstagramLink color="social.instagram" size={size} />
    </Grid>
  );
}

FacebookIcon.propTypes = {
  size: PropTypes.string.isRequired,
};

InstagramIcon.propTypes = {
  size: PropTypes.string.isRequired,
};

YoutubeIcon.propTypes = {
  size: PropTypes.string.isRequired,
};

TwitterIcon.propTypes = {
  size: PropTypes.string.isRequired,
};

HeartLink.propTypes = {
  size: PropTypes.string,
  color: PropTypes.string.isRequired,
};

ReactLink.propTypes = {
  size: PropTypes.string,
  color: PropTypes.string.isRequired,
};

FacebookLink.propTypes = {
  size: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
};

YoutubeLink.propTypes = {
  size: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
};

TwitterLink.propTypes = {
  size: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
};

InstagramLink.propTypes = {
  size: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
};

SocialLink.propTypes = {
  to: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};

HeartLink.defaultProps = {
  size: '1x',
};

ReactLink.defaultProps = {
  size: '1x',
};

SocialLinks.defaultProps = {
  size: '1x',
};

SocialLinks.propTypes = {
  size: PropTypes.string,
};
