import * as React from 'react';
import PropTypes from 'prop-types';
import useMediaQuery from '@mui/material/useMediaQuery';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';

export function TopBarGridContainer({ children }) {
  return (
    <Grid
      container
      alignItems="center"
      sx={{
        p: 0,
        pb: 1,
        borderBottom: 4,
        borderColor: 'secondary.main',
        flexShrink: 0,
        backgroundColor: 'primary.main',
        justifyContent: {
          sm: 'space-between',
          xs: 'space-between',
        },
      }}
    >
      {children}
    </Grid>
  );
}

export function TopBarButton({ text, onClick, borderColor }) {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const textVariant = belowmd ? 'body1' : 'h6';
  return (
    <Button
      sx={{
        color: 'button.main',
        border: 2,
        borderColor,
        borderRadius: 5,
      }}
      variant="outlined"
      onClick={onClick}
    >
      <Typography variant={textVariant}>{text}</Typography>
    </Button>
  );
}

function CloseButton({ onClick }) {
  return (
    <IconButton
      edge="start"
      onClick={onClick}
      aria-label="close"
      sx={{
        fontSize: '3.5rem',
        position: 'absolute',
        top: 0,
        right: 0,
      }}
    >
      <CloseIcon sx={{ fontSize: '3rem', fill: 'muted.main' }} />
    </IconButton>
  );
}

export function TopBarDialog({
  text, open, onClose, onClick, children, wdth,
}) {
  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth={wdth}>
      <DialogTitle>
        {text}
        <CloseButton onClick={onClick} />
      </DialogTitle>
      <DialogContent dividers>{children}</DialogContent>
    </Dialog>
  );
}

export function TopBarLink({ text, children, wdth }) {
  const [open, setOpen] = React.useState(false);
  const handleClickOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <span>
      <TopBarButton text={text} onClick={handleClickOpen} />
      <TopBarDialog
        text={text}
        open={open}
        wdth={wdth}
        onClose={handleClose}
        onClick={handleClose}
      >
        {children}
      </TopBarDialog>
    </span>
  );
}

TopBarLink.propTypes = {
  text: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  wdth: PropTypes.string.isRequired,
};

TopBarDialog.propTypes = {
  text: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  children: PropTypes.node.isRequired,
  wdth: PropTypes.string.isRequired,
  onClose: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
};

TopBarButton.defaultProps = {
  borderColor: 'transparent',
};

TopBarButton.propTypes = {
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  borderColor: PropTypes.string,
};

TopBarGridContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

CloseButton.propTypes = {
  onClick: PropTypes.func.isRequired,
};
