import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Menu from '@mui/material/Menu';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import MenuItem from '@mui/material/MenuItem';

import { TopBarDialog } from './utils';
import About from '../about';
import Contact from '../contact';

export default function BurgerMenu() {
  const navigate = useNavigate();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const [aboutOpen, setAboutOpen] = React.useState(false);
  const [contactOpen, setContactOpen] = React.useState(false);

  const handleAboutOpen = () => setAboutOpen(true);
  const handleAboutClose = () => setAboutOpen(false);

  const handleContactOpen = () => setContactOpen(true);
  const handleContactClose = () => setContactOpen(false);

  const handleClick = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  return (
    <>
      <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>
        <IconButton
          onClick={handleClick}
          size="small"
          sx={{ ml: 2 }}
          aria-controls={open ? 'burger-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
        >
          <MenuIcon sx={{ width: 45, height: 45, color: 'button.main' }} />
        </IconButton>
      </Box>
      <Menu
        anchorEl={anchorEl}
        id="burger-menu"
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        PaperProps={{
          backgroundColor: 'form.input',
          elevation: 0,
          sx: {
            filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
            mt: 1.5,
            '& .MuiAvatar-root': {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            '&:before': {
              content: '""',
              display: 'block',
              position: 'absolute',
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: 'background.paper',
              transform: 'translateY(-50%) rotate(45deg)',
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
      >
        <MenuItem onClick={() => navigate('/memories')}>My Memories</MenuItem>
        <MenuItem onClick={handleAboutOpen}>About</MenuItem>
        <MenuItem onClick={handleContactOpen}>Contact Us</MenuItem>
      </Menu>
      <TopBarDialog
        text="About"
        wdth="lg"
        open={aboutOpen}
        onClose={handleAboutClose}
        onClick={handleAboutClose}
      >
        <About />
      </TopBarDialog>
      <TopBarDialog
        text="Contact"
        wdth="sm"
        open={contactOpen}
        onClose={handleContactClose}
        onClick={handleContactClose}
      >
        <Contact />
      </TopBarDialog>
    </>
  );
}
