import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import useMediaQuery from '@mui/material/useMediaQuery';
import gogolPhoto from '../static/images/gogol.jpg';
import sagnikaPhoto from '../static/images/sagnika.jpg';
import nabarunPhoto from '../static/images/blog_pic.jpg';

const aboutData = {
  gogol: {
    name: 'Shlok Chatterjee (Gogol)',
    image: gogolPhoto,
    text: ['CMO (Chief Motivation Officer)', 'CCC (Chief Chaos Creator)'],
  },
  nabarun: {
    name: 'Nabarun Chatterjee',
    image: nabarunPhoto,
    text: [
      'Full time father to my 2 year old son and husband to my loving wife . Site Reliability Engineer by profession. Memory making travel and finger licking food are what I live for.',
    ],
  },
  sagnika: {
    name: 'Sagnika Dasgupta',
    image: sagnikaPhoto,
    text: [
      "I don't remember my life from 2 year back. Now it is mostly Gogol and a little bit of food, travel, painting, writing and other things.",
    ],
  },
};

function AboutCard({ data }) {
  return (
    <Card sx={{
      minWidth: 250, maxWidth: 350, p: { md: 1, xs: 0 }, m: { md: 1, xs: 0 },
    }}
    >
      <CardMedia
        component="img"
        height="350"
        image={data.image}
        alt={data.name}
        sx={{
          border: { md: 5, xs: 0 },
          borderColor: 'primary.main',
        }}
      />
      <CardContent>
        <Typography variant="h6" sx={{ py: 1 }}>
          {data.name}
        </Typography>
        {data.text.map((part) => (
          <Typography key={part} variant="body2" color="text.secondary">
            {part}
          </Typography>
        ))}
      </CardContent>
    </Card>
  );
}

function About() {
  const reorient = useMediaQuery((theme) => theme.breakpoints.down('lg'));
  return reorient
    ? (
      <Grid container justifyContent="center" alignItems="center">
        <Grid item align="center">
          <AboutCard key="gogol" data={aboutData.gogol} />
        </Grid>
        <Grid item align="center">
          <AboutCard key="nabarun" data={aboutData.nabarun} />
        </Grid>
        <Grid item align="center">
          <AboutCard key="sagnika" data={aboutData.sagnika} />
        </Grid>
      </Grid>
    )
    : (
      <Grid container justifyContent="space-between">
        <Grid item align="center">
          <AboutCard key="nabarun" data={aboutData.nabarun} />
        </Grid>
        <Grid item align="center">
          <AboutCard key="gogol" data={aboutData.gogol} />
        </Grid>
        <Grid item align="center">
          <AboutCard key="sagnika" data={aboutData.sagnika} />
        </Grid>
      </Grid>
    );
}

AboutCard.propTypes = {
  data: PropTypes.shape({
    name: PropTypes.string.isRequired,
    image: PropTypes.string.isRequired,
    text: PropTypes.arrayOf(PropTypes.string).isRequired,
  }).isRequired,
};

export default About;
