import * as React from 'react';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import Logout from '@mui/icons-material/Logout';
import Typography from '@mui/material/Typography';
import ListItemText from '@mui/material/ListItemText';
import Link from '@mui/material/Link';
import commonUtils from '../utils';

function ProfileAvatar({ name, picture, email }) {
  const initials = Array.from(email)[0].toUpperCase();
  return (
    <Avatar alt={name} src={picture} sx={{ referrerPolicy: 'no-referrer' }}>{initials}</Avatar>
  );
}

function ProfileIcon({
  name, email, open, handleClick, avatar,
}) {
  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        textAlign: 'center',
        marginRight: '10px',
      }}
    >
      <Tooltip
        title={(
          <>
            <Typography>{name}</Typography>
            <Typography>{email}</Typography>
          </>
        )}
      >
        <IconButton
          onClick={handleClick}
          size="small"
          sx={{ ml: 2 }}
          aria-controls={open ? 'account-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
        >
          {avatar}
        </IconButton>
      </Tooltip>
    </Box>
  );
}

function ProfileMenu({
  name, open, handleClose, anchorEl, avatar, email,
}) {
  const profileIdentifier = name || email;
  const authURL = process.env.REACT_APP_AUTH_URL;
  return (
    <Menu
      anchorEl={anchorEl}
      id="account-menu"
      open={open}
      onClose={handleClose}
      onClick={handleClose}
      PaperProps={{
        elevation: 0,
        sx: {
          filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
          mt: 1.5,
          '& .MuiAvatar-root': {
            width: 32,
            height: 32,
            ml: -0.5,
            mr: 1,
          },
          '&:before': {
            content: '""',
            display: 'block',
            position: 'absolute',
            top: 0,
            right: 14,
            width: 10,
            height: 10,
            bgcolor: 'background.paper',
            transform: 'translateY(-50%) rotate(45deg)',
            zIndex: 0,
          },
        },
      }}
      transformOrigin={{ horizontal: 'right', vertical: 'top' }}
      anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
    >
      <MenuItem>
        {avatar}
        <ListItemText>{profileIdentifier}</ListItemText>
      </MenuItem>
      <Divider />
      <Link
        href={`https://${authURL}/oauth2/logout`}
        underline="none"
        sx={{ alignItems: 'center', display: 'flex' }}
      >
        <MenuItem>
          <ListItemIcon>
            <Logout fontSize="small" />
          </ListItemIcon>
          <Typography sx={{ color: 'muted.main' }}>Logout</Typography>
        </MenuItem>
      </Link>
    </Menu>
  );
}

export default function Profile() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const userInfo = commonUtils.getUserInfo();
  const handleClose = () => setAnchorEl(null);
  const handleClick = (event) => setAnchorEl(event.currentTarget);
  const userAvatar = (
    <ProfileAvatar name={userInfo.name} picture={userInfo.picture} email={userInfo.email} />
  );

  return (
    <>
      <ProfileIcon
        avatar={userAvatar}
        open={open}
        email={userInfo.email}
        handleClick={handleClick}
        name={userInfo.name}
      />
      <ProfileMenu
        anchorEl={anchorEl}
        avatar={userAvatar}
        handleClose={handleClose}
        name={userInfo.name}
        email={userInfo.email}
        open={open}
      />
    </>
  );
}

ProfileIcon.propTypes = {
  name: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  handleClick: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  avatar: PropTypes.node.isRequired,
};

ProfileMenu.propTypes = {
  name: PropTypes.string.isRequired,
  anchorEl: PropTypes.node.isRequired,
  handleClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  avatar: PropTypes.node.isRequired,
  email: PropTypes.string.isRequired,
};

ProfileAvatar.propTypes = {
  name: PropTypes.string.isRequired,
  picture: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
};
